.logo.icon{background:url("../../images/logo/app-icon.png") center center no-repeat;background-size:contain}.logo.typeface.white{background:url("../../images/logo/logo-white.svg") center center no-repeat;background-size:contain}.logo.typeface.purple{background:url("../../images/logo/logo-purple.svg") center center no-repeat;background-size:contain}.hidden{display:none}.come-in{-webkit-transform:translateY(150px);-ms-transform:translateY(150px);transform:translateY(150px);-webkit-animation:come-in 0.8s ease forwards;animation:come-in 0.8s ease forwards}.come-in:nth-child(odd){-webkit-animation-duration:0.6s;animation-duration:0.6s}.already-visible{-webkit-transform:translateY(0);-ms-transform:translateY(0);transform:translateY(0);-webkit-animation:none;animation:none}@-webkit-keyframes come-in{to{-webkit-transform:translateY(0);transform:translateY(0)}}@keyframes come-in{to{-webkit-transform:translateY(0);transform:translateY(0)}}



