.signup-form .content-wrapper .sub-heading .logo {
  width: 25.3rem;
  height: 5.6rem; }

.signup-form .content-wrapper .form {
  display: inline-grid;
  grid-template-rows: auto auto auto;
  justify-items: center;
  grid-gap: 3rem; }
  .signup-form .content-wrapper .form .button {
    width: 16rem; }

@media only screen and (min-width: 768px) {
  .signup-form .content-wrapper.signup-wrapper .heading {
    max-width: 45.8rem;
    font-size: 3.0rem; }
  .signup-form .content-wrapper.signup-wrapper .sub-heading .logo {
    width: 36.1rem;
    height: 8.1rem; } }

@media only screen and (min-width: 1024px) {
  .signup-form .content-wrapper.signup-wrapper .form {
    grid-template-columns: repeat(2, minmax(30rem, 34.1rem)) 16rem;
    grid-template-rows: auto;
    justify-items: stretch; } }



